/*MEDIA QUERIES*/

@mixin media($size) {
    @if $size ==xs {
        @media (min-width: 320px) {
            @content;
        }
    }

    @else if $size ==sm {
        @media (min-width: 640px) {
            @content;
        }
    }

    @else if $size ==md {
        @media (min-width:768px) {
            @content;
        }
    }

    @else if $size ==lg {
        @media (min-width: 1024px) {
            @content;
        }
    }
    @else if $size ==xl {
        @media (min-width: 1284px) {
            @content;
        }
    }

}